.pg{
  height:100vh;
  overflow: hidden;
	color: #ef858c; /* 文字色 */
	position: relative;
	text-shadow: 0px 0px 1px #fff, 0px 0px 3px #fff, 0px 0px 4px #fff, 0 0 0.5px #fff; /* 文字の影 */
  background:
        radial-gradient(
            #f0e68cb5 40px,
            transparent 0,
            transparent 100%
        ),
        radial-gradient(
            #a4d2e4a1 40px,
            transparent 0,
            transparent 100%
        ),
        radial-gradient(
            #ffc0cbb8 40px,
            transparent 0,
            transparent 100%
        ), snow;
  background-blend-mode: multiply;
  background-size: 100px 100px;
  background-position: 0 0, 33px 33px, -33px -33px;
  background-color: #f5d5da; /* 背景色 */
}
.pg:before{
	content: '\FF0A*'; /*花に見せかけるためのアスタリスク*/
	color: #fff; /* アスタリスクの色 */
	display: inline-block;
	font-size: 60px; /* アスタリスクの大きさ */
	font-weight: bold;
	margin-right: 10px;
	position: absolute;
	top: 0px;
	left: 0px;
	transform: rotate(20deg);
	-moz-transform: rotate(20deg);
	-webkit-transform: rotate(20deg);
	-o-transform: rotate(20deg);
	text-shadow: 0px 0px 3px #fff, 0px 0px 2px #fff, 0 0 0.5px #fff; /* アスタリスク周りの影 */
}
.pg:after{
	border-bottom: 2px dotted #fff; /* 下線 */
	content: '';
	position: absolute;
	bottom: 3px;
	left: 3px;
	right: 3px;
}
